import React from 'react'
import "../css/Home.css"
import { Card, CardGroup, Carousel } from 'react-bootstrap';
import "bootstrap/dist/css/bootstrap.min.css"
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import Footerhead from '../cofig/Footer';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function HomePage() {
  const notify = () => toast.info("Signup to see my Gallery ");
    return (
        <div className=" bg-pink-200" >
             <div class="hello">
                            <h1 className="text-4xl text-bold text-white p-10 text-center">ASPIRE</h1>
                            <p className="text-white p-25"><marquee>Classy! Awosome! Photos! </marquee> </p>
                        </div>
               <div className="w-10/12 mt-3 m-auto">
                   
               <motion.h1 initial={{x:2000}} animate={{ x: 1, }} transition={{ duration: 1 }}  className= "bg-blue-100 text-4xl text-bold text-black p-10 text-center">WELCOME TO MY GALLERY!</motion.h1> 
               <div className="w-1/2 m-auto   mobileform">
              
               <Carousel>
  <Carousel.Item interval={1000}>
    <img
      className="d-block w-100"
      src="images/14.jpg"
      alt="First slide"
    />
    <Carousel.Caption>
     
      <p>Macro without Macro</p>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item interval={500}>
    <img
      className="d-block w-100"
      src="images/11.jpg"
      alt="Second slide"
    />
    <Carousel.Caption>
     
      <p>Closeup</p>
    </Carousel.Caption>
  </Carousel.Item>
  <Carousel.Item>
    <img
      className="d-block w-100"
      src="images/13.jpg"
      alt="Third slide"
    />
    <Carousel.Caption>
      
      <p>Zoom</p>
    </Carousel.Caption>
  </Carousel.Item>
</Carousel>
</div>
</div>
<div className="bg-indigo-300">
<hr className="bg-black"></hr>
  <img className="haha m-auto" src="images/15.jpg" width="640px " height="500px" ></img>
<p className="font-semibold mt-3 m-auto w-1/2 t">Welcome to my world ! Here you can find some awosome 
collection of different type of photos.All the photos are clicked by me using my smartphone ,no other camera is used.
Hope you will love them.Thankyou for visting😊
<br></br>                         You can go to gallery section to see more photos 
</p>
<hr className="bg-black"></hr>


</div>
<CardGroup className="p-2">
  <Card className="p-2">
    <Card.Img variant="top" src="images/16.jpg" />
    <Card.Body>
      <Card.Title>Bloom</Card.Title>
      <Card.Text>
      Watch me bloom like spring after a long winter
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted">Last updated 3 mins ago</small>
    </Card.Footer>
  </Card>
  <Card className="p-2">
    <Card.Img variant="top" src="images/17.jpg" />
    <Card.Body>
      <Card.Title>Drop</Card.Title>
      <Card.Text>
      Thoughts are like drops of water: with our thoughts we can drown in a sea of negativity, or we can float on the ocean of life
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted">Last updated 3 mins ago</small>
    </Card.Footer>
  </Card>
  <Card className="p-2">
    <Card.Img variant="top" src="images/18.jpg"/>
    <Card.Body>
      <Card.Title>Buds</Card.Title>
      <Card.Text>
      My best buds, through and through.
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <small className="text-muted">Last updated 3 mins ago</small>
    </Card.Footer>
  </Card>
</CardGroup>
<div className="flex justify-center mb-2">


<button className="  rounded-lg p-2 text-white text-black  bg-gradient-to-r from-red-400 to-blue-500 hover:from-pink-500 hover:to-yellow-500" onClick={notify}>Click Me!</button>
        <ToastContainer />
        </div>

<Footerhead></Footerhead>

</div>         
                      
    
      
    )
}

export default HomePage
